<template>
	<div>
		<HeaderComponent :view-title="dataTitle"></HeaderComponent>
		<div id="dataStatistics-main">
			<section class="user-profile">
				<h4>
					<svg class="iconData" aria-hidden="true">
	  					<use xlink:href="#icon-line-vertical"></use>
					</svg>用户概况(注册到当前)
				</h4>
				<ul class="user-profile-container clear">
					<li class="left clear">
						<p>
							<span>133</span>
							<span> (场)</span>
						</p>
						<p>创建活动</p>
					</li>
					<li class="left clear">
						<p>
							<span>1000</span>
							<span> (人)</span>
						</p>
						<p>兑奖人数</p>
					</li>
				</ul>
			</section>
			<section class="activity-transformation">
				<h4>
					<svg class="iconData" aria-hidden="true">
	  					<use xlink:href="#icon-line-vertical"></use>
					</svg>活动转化
				</h4>
				<ul class="activity-transformation-container">
					<li class="clear">
						<p class="left clear">
							<svg class="iconData" aria-hidden="true">
	  							<use xlink:href="#icon-fenxiang1"></use>
							</svg>
							<span>活动分享数</span>
						</p>
						<p class="right clear">
							<span>100w</span>
							<span> (人)</span>
						</p>
					</li>
					<li class="clear">
						<p class="left clear">
							<svg class="iconData" aria-hidden="true">
	  							<use xlink:href="#icon-renwu"></use>
							</svg>
							<span>活动参与数</span>
						</p>
						<p class="right clear">
							<span>100w</span>
							<span> (人)</span>
						</p>
					</li>
					<li class="clear">
						<p class="left clear">
							<svg class="iconData" aria-hidden="true">
	  							<use xlink:href="#icon-shuju3"></use>
							</svg>
							<span>活动浏览量</span>
						</p>
						<p class="right clear">
							<span>99w</span>
							<span> (人)</span>
						</p>
					</li>
				</ul>
			</section>
			<section class="human-activity">
				<h4>
					<svg class="iconData" aria-hidden="true">
	  					<use xlink:href="#icon-line-vertical"></use>
					</svg>人气活动
				</h4>
				<div class="human-activity-container">
					<section class="clear">
						<img class="left clear" src="../../assets/images/user.jpg">
						<div class="right clear">
							<p>幸运大转盘</p>
							<p>
								<span>浏览人数：</span><span class="human-num">999</span>
								<span>参加人数：</span><span class="human-num">999</span>
							</p>
							<p>创建时间：2018-08-24 14:32</p>
						</div>
					</section>
					<section class="clear">
						<img class="left clear" src="../../assets/images/user.jpg">
						<div class="right clear">
							<p>幸运大转盘</p>
							<p>
								<span>浏览人数：</span><span class="human-num">999</span>
								<span>参加人数：</span><span class="human-num">999</span>
							</p>
							<p>创建时间：2018-08-24 14:32</p>
						</div>
					</section>
					<section class="clear">
						<img class="left clear" src="../../assets/images/user.jpg">
						<div class="right clear">
							<p>幸运大转盘</p>
							<p>
								<span>浏览人数：</span><span class="human-num">999</span>
								<span>参加人数：</span><span class="human-num">999</span>
							</p>
							<p>创建时间：2018-08-24 14:32</p>
						</div>
					</section>
				</div>
			</section>
		</div>
	</div>
</template>

<script type="text/javascript">
	import HeaderComponent from '../../components/header/header';
	export default {
		dataTitle:Object,
		data(){
			return{
				dataTitle:{
					name:'数据统计',
					showCode:1
				}
			}
		},
		mounted(){},
		components:{
			HeaderComponent
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/mixin';
	.iconData {
	   width: 0.9rem; height: 0.9rem;
	   vertical-align: -0.15em;
	   fill: currentColor;
	   overflow: hidden;
	   color:#F35353;
	}
	#dataStatistics-main{
		margin-top:2.2rem;
		.user-profile{
			h4{
				@include sc(0.7rem,#4C4C4C);
				height:1.8rem;
				line-height:1.8rem;
				padding-left:0.2rem;
				svg.iconData{
					color:#F35353;
				}
			}
			ul.user-profile-container{
				background:#fff;
				&::before,&::after{
					display: table;
					content: "";
				}
				li{
					width:50%;
					margin:1rem 0;
					&:first-child{
						border-right:1px solid #D9D9D9;
					}
					p{
						text-align: center;
						@include sc(0.5rem,#717171);
						&:first-child{
							span:first-child{
								@include sc(0.9rem,#333);
							}
							span:last-child{
								@include sc(0.5rem,#717171);
							}
						}
					}
				}
			}
		}
		.activity-transformation{
			h4{
				@include sc(0.7rem,#4C4C4C);
				height:1.8rem;
				line-height:1.8rem;
				padding-left:0.2rem;
				svg.iconData{
					color:#F35353;
				}
			}
			ul.activity-transformation-container{
				background:#fff;
				padding:0 0.5rem;
				li{
					p{
						height:2.3rem;
						line-height: 2.3rem;
					}
					p.left{
						svg.iconData{
							width:1.5rem;
							height:1.5rem;
							vertical-align: middle;
						}
						span{
							@include sc(0.7rem,#4C4C4C);
						}
					}
					p.right{
						span:first-child{
							@include sc(0.9rem,#333);
						}
						span:last-child{
							@include sc(0.6rem,#717171);
						}
					}
				}
			}
		}
		.human-activity{
			h4{
				@include sc(0.7rem,#4C4C4C);
				height:1.8rem;
				line-height:1.8rem;
				padding-left:0.2rem;
				svg.iconData{
					color:#F35353;
				}
			}
			.human-activity-container{
				background:#fff;
				padding:0 0.5rem;
				&::before,&::after{
					display: table;
					content: "";
				}
				section{
					margin-top:0.5rem;
					margin-bottom:0.7rem;
					img.left{
						display: block;
						width:3rem;
						height:3rem;
					}
					div.right{
						width:11.3rem;
						P{
							&:first-child{
								margin-bottom: 0.4rem;
								@include sc(0.7rem,#4C4C4C);
							}
							&:last-child{
								@include sc(0.6rem,#ACACAC);
							}
							span{
								@include sc(0.6rem,#ACACAC);
							}
							span.human-num{
								color:#F78383;
							}
						}
					}
				}
			}
		}
	}
</style>


